var checkCode = function (val) {
  var p = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
  var factor = [ 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2 ];
  var parity = [ 1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2 ];
  var code = val.substring(17);
  if(p.test(val)) {
      var sum = 0;
      for(var i=0;i<17;i++) {
          sum += val[i]*factor[i];
      }
      if(parity[sum % 11] == code.toUpperCase()) {
          return true;
      }
  }
  return false;
}
var checkDate = function (val) {
  var pattern = /^(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)$/;
  if(pattern.test(val)) {
      var year = val.substring(0, 4);
      var month = val.substring(4, 6);
      var date = val.substring(6, 8);
      var date2 = new Date(year+"-"+month+"-"+date);
      if(date2 && date2.getMonth() == (parseInt(month) - 1)) {
          return true;
      }
  }
  return false;
}
var checkProv = function (val) {
  var pattern = /^[1-9][0-9]/;
  var provs = {11:"",12:"",13:"",14:"",15:"",21:"",22:"",23:" ",31:"",32:"",33:"",34:"",35:"",36:"",37:"",41:"",42:" ",43:"",44:"",45:"",46:"",50:"",51:"",52:"",53:"",54:" ",61:"",62:"",63:"",64:"",65:"",71:"",81:"",82:""};
  if(pattern.test(val)) {
      if(provs[val]) {
          return true;
      }
  }
  return false;
}
var checkID = function (val) {
  if(checkCode(val)) {
      var date = val.substring(6,14);
      if(checkDate(date)) {
          if(checkProv(val.substring(0,2))) {
              return true;
          }
      }
  }
  return false;
}

export default (rule, value, model) => {
	if (!value) {
		if (rule.required) return rule.makeError('required')
		return
	}

  if (value && !checkID(value)) return rule.makeError('idnumber')
}