import List from '../utils/list.js'

export default {
  props: {
    name: String,
    title: {
      type: String,
      default: ''
    },
    tools: {
      type: Array,
      default: () => []
    },
    fields: {
      type: Array,
      default: () => []
    },
    layout: {
      type: Array,
      default: () => []
    },
    boxComponent: {
      type: String,
      default: 'Box'
    },
    boxOptions: {},
    value: Object,
    static: {
      type: Boolean,
      default: false
    }, //
    staticSuffix: {
      type: String,
      default: '_static'
    },
    buttons: {},
    labelWidth: {
      type: Number,
      default: 150
    },
    labelAlign: {
      type: String,
      default: 'right'
    },
    theme: {
      type: String,
      default: 'default'
    },
    validateResult: {},
    labelDir: { // horizontal  vertical
      type: String,
      default: 'horizontal'
    },
    hidden: { // 
      type: Boolean,
      default: false
    }
  },

  computed: {
    rows () {
      let r = [], name
      for (let row of this.layout) {
        let new_r = []
        let span = 24 / row.length

        //col
        for (let col of row) {
          if (typeof col === 'object') {
            name = col.name
            span = col.colspan || span
          } else {
            name = col
          }
          let f = List.get(this.fields, name, 'name')
          if (!f) throw new Error(`Can't find field ${name} in fields, please check if the name is not correct between layout and fields`)
          let field = Object.assign({colspan: span,
            labelWidth: this.labelWidth,
            labelAlign: col.labelAlign || f.labelAlign || this.labelAlign,
            static: col.static || this.static,
            hidden: this.hidden || col.hidden,
          }, f)

          if (!field.hidden)
            new_r.push(field)
        }
        if (new_r.length > 0)
          r.push(new_r)
      }
      return r
    },

    btns () {
      if (Array.isArray(this.buttons)) return this.buttons
      else return this.buttons.items
    },

    size () {
      return this.buttons.size || 'default'
    },

    themeClasses () {
      let klass = {}
      klass[`build-layout-theme-${this.theme}`] = true
      klass[`u-layout-${this.labelDir}`] = true
      return klass
    }
  }
}
